import React from 'react';
import PropTypes from 'prop-types';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';

import {Link} from "react-router-dom";
import { bindActionCreators } from 'redux';
import {menuActions} from '../../redux/actions';

import { connect } from 'react-redux';

const LeftNavigation = props => (
	<Drawer
		variant="persistent"
		anchor="left"
		open={props.openNavigation}
	>
		<div>
			<IconButton onClick={() => props.toggleNavigation()}>
				 <ChevronLeftIcon />
			</IconButton>
		</div>
		<Divider />
		<List>
		<Link to={'/admin'}>Admin Page</Link>
			</List>
		<Divider />
		<List>
			{['Inbox', 'Starred', 'Send email', 'Drafts'].map((text, index) => (
				<ListItem button key={text}>
					<ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
					<ListItemText primary={text} />
				</ListItem>
			))}
		</List>
		<Divider />
		<List>
			{['All mail', 'Trash', 'Spam'].map((text, index) => (
				<ListItem button key={text}>
					<ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
					<ListItemText primary={text} />
				</ListItem>
			))}
		</List>
	</Drawer>
);

LeftNavigation.propTypes = {
	toggleNavigation: PropTypes.func.isRequired,
	openNavigation: PropTypes.bool.isRequired,
};


const mapStateToProps =  (state, props) => {
	return({ openNavigation: state.menu.openNavigation })
};

const mapDispatchToProps = dispatch => {
	return ({
		toggleNavigation: bindActionCreators(menuActions.toggleNavigation, dispatch)
	});
};

export default connect(mapStateToProps, mapDispatchToProps)(LeftNavigation);