import { menuConstants } from '../constants';

export const menuActions = {
	toggleNavigation,
	increment,
	decrement
};


function toggleNavigation() {
	return({type: menuConstants.TOGGLE_NAVIGATION});
}

function increment() {
	return function (dispatch, getState) {
		const state = getState();

		dispatch({type: menuConstants.INC});
		return state.value + 1;
	}
}

function decrement() {
	return function (dispatch, getState) {
		dispatch({type: menuConstants.DEC});
		return state.value - 1;
	}
}
