import React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

import { bindActionCreators } from 'redux';

import {connect} from 'react-redux';
import {Link} from "react-router-dom";
import {menuActions, userActions} from "../../redux/actions";

class Header extends React.Component {
	componentDidMount() {
		this.props.getUsers();
	}

	render() {
		return(
			<div className="header">
				<AppBar position="static">
					<Toolbar className="d-flex flex-row align-items-center justify-content-between">
						<div className="d-flex flex-row align-items-center">
							<IconButton edge="start" color="inherit" aria-label="menu" onClick={()=>this.props.toggleNavigation()}>
								<MenuIcon/>
							</IconButton>
							<Typography variant="h6">
								Welcome to Planner - {this.props.userLogged && this.props.userLogged.firstName}
							</Typography>
						</div>
						<Button color="inherit"><Link to="/login">Logout</Link></Button>
					</Toolbar>
				</AppBar>
			</div>
		);
	}
}

Header.propTypes = {
	toggleNavigation: PropTypes.func.isRequired,
	openNavigation: PropTypes.bool.isRequired,
};


const mapStateToProps =  (state, props) => {
	const {authentication} = state;
	const {userLogged} = authentication;

	return({ openNavigation: state.menu.openNavigation, userLogged })
};

const mapDispatchToProps = dispatch => {
	return ({
		toggleNavigation: bindActionCreators(menuActions.toggleNavigation, dispatch),
		getUsers: userActions.getAll,
	});
};

export default connect(mapStateToProps, mapDispatchToProps)(Header);