import {menuConstants}from '../constants';

const defaultState = {
	value: false,
};


export default function reducer (state = defaultState, action) {
	switch (action.type) {
		case menuConstants.INC:
			return {
				...state,
				value: action.payload,
			};
		case menuConstants.DEC:
			return {
				...state,
				value: action.payload,
			};
		default:
			return state;
	}
}
