import React from 'react';
import {connect} from 'react-redux';
import {Button} from '@material-ui/core'
import {userActions} from '../redux/actions';
import Header from "./common/Header";
import Footer from "./common/Footer";
import LeftNavigation from "./common/LeftNavigation";

class AdminPage extends React.Component {
	componentDidMount() {
		this.props.getUsers();
	}

	handleDeleteUser(id) {
		return (e) => this.props.deleteUser(id);
	}

	render() {
		const {user, users} = this.props;
		return (
			<div>
				<Header/>
				<div className="body admin">
					<LeftNavigation/>
					<div className="container">
						<div className="text-form-info">
							<h2>Hi {user && user.firstName}!</h2>
							<h2>All registered users:</h2>
							{users.loading && <em>Loading users...</em>}
							{users.error && <span className="text-danger">ERROR: {users.error}</span>}
						</div>

						{users.items &&
						<ul className="admin-form">
							{users.items.map((user, index) =>
								<li key={user.id}>
									{user.firstName + ' ' + user.lastName}
									{
										user.deleting ? <em> - Deleting...</em>
											: user.deleteError ? <span className="text-danger"> - ERROR: {user.deleteError}</span>
											: <span> - <Button onClick={this.handleDeleteUser(user.id)}>Delete</Button></span>
									}
								</li>
							)}
						</ul>
						}
					</div>
				</div>
				<Footer/>
			</div>
		);
	}
}

function mapState(state) {
	const {users, authentication} = state;
	const {user} = authentication;
	return {user, users};
}

const actionCreators = {
	getUsers: userActions.getAll,
	deleteUser: userActions.delete
};

export default connect(mapState, actionCreators)(AdminPage);